.navbar__cart {
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  padding: 10px 20px;
  
  color: #393E46;
  cursor: pointer;
  border-radius: 8px;
  background-color: #EEEEEE;
  transition: all 500ms;
}

.navbar__cart:hover {
  background-color: #AAD8D3;
}

.navbar__cart-icon {
  width: 20px;
  height: 20px;
}

.navbar__cart-count {
  margin-left: 10px;
  font-size: 18px;
}